// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'

Vue.config.productionTip = false

const planObj = [
  {
    Plan: {
      "Node Type": "Seq Scan",
      "Parallel Aware": false,
      "Relation Name": "device_dim",
      Schema: "public",
      Alias: "device_dim",
      "Startup Cost": 0.0,
      "Total Cost": 204.56,
      "Plan Rows": 11456,
      "Plan Width": 26,
      "Actual Startup Time": 0.023,
      "Actual Total Time": 2.954,
      "Actual Rows": 11426,
      "Actual Loops": 1,
      Output: [
        "id",
        "serial_number",
        "device_type_id",
        "manufacturer",
        "model",
      ],
      "Shared Hit Blocks": 90,
      "Shared Read Blocks": 0,
      "Shared Dirtied Blocks": 0,
      "Shared Written Blocks": 0,
      "Local Hit Blocks": 0,
      "Local Read Blocks": 0,
      "Local Dirtied Blocks": 0,
      "Local Written Blocks": 0,
      "Temp Read Blocks": 0,
      "Temp Written Blocks": 0,
    },
    "Planning Time": 0.569,
    Triggers: [],
    "Execution Time": 4.388,
  },
];

Vue.prototype.$planData = Vue.observable({
  source: JSON.stringify(planObj),
  query: '',
});

export function setPlan (plan) {
  Vue.prototype.$planData.source = plan;
  console.log(Vue.prototype.$planData);
}

window.setPlan = setPlan;

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
});



