<template>
  <div class="hello">
    <pev2 v-if="$planData.source" :plan-source="$planData.source" :plan-query="$planData.query" :key="$planData.source" ></pev2>
  </div>
</template>

<script>
import pev2 from "pev2";

export default {
  name: "HelloWorld",
  data () {
    return {
      msg: "Welcome to Your Vue.js App",
      query: ""
    };
  },
  components: {
    pev2: pev2,
  },
  updated: function () {
    console.log("update!", this);
    console.log(this.$planData.source);
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.hello {
  height: 100vh;
  width: 100vw;
}
.plan-container {
  height: 100%;
}
</style>



